import React              from 'react'
import { Link }           from 'gatsby'
import { graphql }        from 'gatsby'
import { useStaticQuery } from 'gatsby'
import styles             from './header.module.scss'

const Header = () => {
    const data = useStaticQuery(graphql`
    query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `)

    return (
        <header className={styles.headerElements}>
            <div>
              <Link className={styles.title} activeClassName={styles.titleActiveItem} to="/"> {data.site.siteMetadata.title} </Link>
            </div>

            <nav>
                <ul className={styles.navList}>
                    <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/resume"> Resume </Link> </li>
                </ul>
            </nav>
        </header>
    )
}

export default Header